<template>
    <section>
        <el-button class="add" @click="addNews">添加新闻</el-button>
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
                prop="id"
                label="id"
                width="180">
            </el-table-column>
            <el-table-column
                prop="headline"
                label="大标题"
               >
            </el-table-column>
            <el-table-column
                prop="subtitle"
                label="副标题">
            </el-table-column>
            <el-table-column
                fixed="right"
                label="操作"
                width="100">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="medify(scope.row)">修改</el-button>
                    <el-button type="text" size="small" @click="deleteNews(scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="page">
              <el-pagination
            background
            layout="prev, pager, next"
            @current-change="pageChange"
            :page-size="10"
            :total="total">
        </el-pagination> 
        </div>
        <el-dialog
            title="添加新闻"
            :visible.sync="dialogVisible"
            width="60%"
            :before-close="handleClose">
            <div class="news">
              <h1>大标题</h1> 
              <el-input v-model="headline" placeholder="请输入内容"></el-input>
            </div>
            <div class="news">
              <h3>副标题</h3> 
              <el-input v-model="subtitle" placeholder="请输入内容"></el-input>
            </div>
            <div class="news">
              <h3>作者</h3> 
              <el-input v-model="author" placeholder="请输入内容"></el-input>
            </div>
            <div class="news">
              <h5>新闻内容</h5> 
              <vue-editor v-model="content"></vue-editor>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="save">保存</el-button>
            </span>
        </el-dialog>    
    </section>    
</template>
<script>
import { VueEditor } from 'vue2-editor'
import { saveNews,getNewsList ,getNewsDes,updateNews,deleteNews,getNewsPage} from '../../api/api';
export default {
    data() {
        return {
            tableData: [],
            dialogVisible: false,
            headline:"",
            subtitle:"",
            author:"",
            content:"",
            id:0,
            nowPage:1,
            total:0
        }
    },
    mounted(){
       this.init(this.nowPage);
    },
    methods:{
        init(nowPage){
            getNewsList({page:nowPage}).then((res)=>{
                if(res.code){
                    this.tableData=res.result; 
                }
            })
            getNewsPage().then((res)=>{
                if(res.code){
                  this.total=res.result; 
                }
            })
        },
        addNews(){
            this.dialogVisible=true;
        },
        handleClose(){
          this.dialogVisible=false;
        },
        medify(obj){
            this.dialogVisible=true;
            getNewsDes({
                id:obj.id
            }).then((res)=>{
                    if(res.code){
                        let data=res.result
                        this.id=  data.id
                        this.headline=  data.headline   
                        this.subtitle=  data.subtitle   
                        this.author=  data.author     
                        this.content=  data.content   
                    }
            })
        },
        pageChange(page){
            console.log(page,"gaibianle")
            this.init(page);
            this.nowPage=page;
        },
        deleteNews(obj){
            console.log(111)
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
                }).then(() => {
                    deleteNews({
                      id:obj.id
                    }).then((res)=>{
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                        if(this.tableData.length===1&&this.nowPage>1){
                          this.nowPage--;
                        }
                        this.init(this.nowPage);
                    }).catch(()=>{
                        this.$message({
                            type: 'info',
                            message: '删除失败!'
                        });
                    })
                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
                });
        },
        save(){
            if(this.headline===""||this.headline===null){
               return this.$message({
                        message: '大标题不能为空',
                        type: 'warning'
                    });
            }
            if(this.subtitle===""||this.subtitle===null){
               return this.$message({
                        message: '副标题不能为空',
                        type: 'warning'
                    });
            } 
            if(this.author===""||this.author===null){
               return this.$message({
                        message: '副标题不能为空',
                        type: 'warning'
                    });
            } 
            if(this.content===""||this.content===null){
               return this.$message({
                        message: '内容不能为空',
                        type: 'warning'
                    });
            }
            if(this.id>0){
                updateNews({
                    id:this.id,
                    headline:this.headline,
                    subtitle:this.subtitle,
                    author:this.author,
                    content:this.content
                }).then((result)=>{
                    if(result.code){
                            this.$message({
                                message: '更新成功',
                                type: 'success'
                            });
                            this.init(this.nowPage);
                            this.dialogVisible=false;
                    }else{
                            this.$message({
                                message: '更新失败',
                                type: 'warning'
                            });
                    }
                })
            }else{
                saveNews({
                    headline:this.headline,
                    subtitle:this.subtitle,
                    author:this.author,
                    content:this.content
                }).then((result)=>{
                    if(result.code){
                            this.$message({
                                message: '添加成功',
                                type: 'success'
                            });
                            this.init(this.nowPage);
                            this.dialogVisible=false;
                    }else{
                            this.$message({
                                message: '添加失败',
                                type: 'warning'
                            });
                    }
                })
            }
        }
    },
    components: {
      VueEditor
    },
}
</script>
<style lang="less" scoped>
  section{
      .add{
          margin: 10px;
      }
      .page{
          text-align: center;
          width: 500px;
          position: absolute;
          top: 750px;
          left:0px;
          right: 0;
          margin: auto;
      }
  }
</style>
